import { Carousel, WingBlank } from 'antd-mobile';
import React from 'react'
class CarouselApp extends React.Component {
  state = {
    data: ['1', '2', '3','4'],
    imgHeight:500, 
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: ['i1/87/O1CN01zEXcri1CVt0RlKGq0_!!87-0-lubanu', 'i2/16/O1CN013zCVeA1BzMt4UW105_!!16-0-lubanu', 'i3/149/O1CN01wekXPw1CyHZ23AC4R_!!149-0-lubanu','i4/118/O1CN01qU6Cv41Ck5Hotl3K6_!!118-0-luban'],
      });
    }, 1000);
  }
  render() {
    return (
      <WingBlank>
        <Carousel
          autoplay
          infinite
          beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          afterChange={index => console.log('slide to', index)}
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="http://www.alipay.com"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={`http://gw.alicdn.com/imgextra/${val}.jpg`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
      </WingBlank>
    );
  }
}


export default CarouselApp;