import React, { Component } from 'react';
import {HashRouter as Router,Redirect,Route,NavLink,Switch} from 'react-router-dom'
import Home from './taobao-task3/Home';
import Mes from './taobao-task3/Message';
import My from './taobao-task3/MyTaobao';
import ShCa from './taobao-task3/ShoppingCart';
import TinyB from './taobao-task3/TinyBuying';
import Goods from './taobao-task3/Goods';

import Shangpu from './taobao-task3/DianPu'
import Kefu from './taobao-task3/KeFu'
import Shoucang from './taobao-task3/ShouCang'
import Putincart from './taobao-task3/PutInCart'
import Buyitnow from './taobao-task3/BuyItNow'

import Tab from './taobao-task3/MyTab1'
import Mytab from './taobao-task3/Mytab';
import Search from './taobao-task3/Search';
import {Button,TabBar} from 'antd-mobile';

const App=()=>{
    return<Router>
        <Switch>
            <Route exact path='/' component={Home}/>
            <Route path='/shoppingCart' component={ShCa} />
            <Route path='/TinyBuying' component={TinyB} />
            <Route path='/message' component={Mes} />
            <Route path='/myTaobao' component={My} />

            <Route path='/shangpu' component={Shangpu}/>
            <Route path='/kefu' component={Kefu} />
            <Route path='/shoucang' component={Shoucang} />
            <Route path="/putincart" component={Putincart} />
            <Route path='/buyitnow' component={Buyitnow} />

            <Route path='/search' component={Search}/>
            <Route path='/goods' component={Goods}/>
            <Route render={()=><Redirect to='/'/>} />
        </Switch>
        {/* <Mytab/> */}
    </Router>
}

export default App;
