import React, { Component } from 'react';
import {Button,TabBar} from "antd-mobile";
import  {withRouter}  from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        selectedTab: 'home',
        hidden:false,
        fullScreen:false
      };
    }
  
   
    render() {
      const { pathname } = this.props.location;
      console.log(pathname.split('/'));
      if(pathname.split('/').length>2){
          return null;
      }
      return (
        <div style= {{ position: 'fixed', 
        width: '100%', 
        bottom: 0}}>
          <TabBar
            unselectedTintColor="#33A3F4"
            tintColor="#33A3F4"
            barTintColor="white"
          >
            <TabBar.Item
              icon={
              <i style={{fontSize:25}} className='iconfont icon-icon_huabanfuben'></i>
              }
              selectedIcon={
              <i style={{fontSize:25}} className='iconfont icon-shouye'></i>
              }
              title="首页"
              key="Home"
              selected={pathname === '/'}
              onPress={() => {
                this.props.history.push('/');
              }}
              data-seed="logId"
            >
  
            </TabBar.Item>
            <TabBar.Item
              icon={
              <i style={{fontSize:25}} className='iconfont icon-weitao1'></i>
              }
              selectedIcon={
                <i style={{fontSize:25}} className='iconfont icon-weitao'></i>

              }
              title="微淘"
              key="TinyBuying"

              selected={pathname=== '/tinyBuying'}
              onPress={() => {
                this.props.history.push('/tinyBuying')
              }}
              data-seed="logId1"
            >
            </TabBar.Item>
            <TabBar.Item
              icon={
                <i style={{fontSize:25}} className='iconfont icon-62'></i>

              }
              selectedIcon={
                <i style={{fontSize:25}} className='iconfont icon-xinxi3'></i>

              }
              title="消息"
              key="Message"
              selected={pathname === '/message'}
              onPress={() => {
              this.props.history.push('/message')
              }}
              data-seed="logId2"
            >
            </TabBar.Item>
            <TabBar.Item
              icon={
                <i style={{fontSize:25}} className='iconfont icon-gouwuchekong'></i>
              }
              selectedIcon={
                <i style={{fontSize:25}} className='iconfont icon-gouwucheman'></i>
              }
              title="购物车"
              key="ShoppingCart"
              selected={pathname === '/shoppingCart'}
              onPress={() => {
              this.props.history.push('/shoppingCart')
              }}
              data-seed="logId3"
            >
            </TabBar.Item>
            <TabBar.Item
              icon={
              <i style={{fontSize:25}} className='iconfont icon-wode'></i>
              }
              selectedIcon={
              <i style={{fontSize:25}} className='iconfont icon-wode1'></i>
              }
              title="我的淘宝"
              key="MyTaobao"
              selected={pathname === '/myTaobao'}
              onPress={() => {
                this.props.history.push('/myTaobao')
              }}
              data-seed="logId4"
            >
            </TabBar.Item>
          </TabBar>
        </div>
      );
    }
  }

export default withRouter (Mytab);