import React, {Component} from 'react';
// import { Router } from 'react-router-dom';
import { Route,withRouter } from 'react-router-dom'
import './home.css'
import Tab from './MyTab1'
class Goods extends Component{
    constructor(props){
        super(props);
        this.state = {};
      }
    render(){
        return(
            <div className="go-img">
                <p onClick={() => {
                   this.props.history.push('/')}}>返回</p>
               <div 
              onClick={() => {
                this.props.history.push('/goods');
              }}
              className="ho-img">
                <img
                style={{width:'200%',
                    height:'auto'
                }}
              src='https://gw.alicdn.com/bao/uploaded/i4/519286239/O1CN01YGF5ZO1vxVgO1sMIR_!!0-item_pic.jpg_500x500q90.jpg_.webp' />
              {/* <span>【王一博】百雀羚帧颜淡纹修护抗初老人参多肽视</span>
              <a className='money'>￥1,354</a><a className='person'>369人已购</a> */}
          </div>
                <Route>
                    <Tab/>    
                </Route>               
            </div>            
        )
    }
 
}

export default Goods;