import React from 'react'
import { Route,withRouter } from 'react-router-dom'
import MyTab from './Mytab'
const MyTaobao = () => {
    return (
        <div>
           MyTaobao
           <i style={{fontSize:25,color:'red'}} className='iconfont icon-wode'></i>
           <Route>
               <MyTab/>
           </Route>
        </div>
    )
}

export default MyTaobao